.About {
  height: 100%;
  background-image: url(https://drive.google.com/uc?id=0Bw5fMNSAnPnJbHRUd09veGdIbjg);
  background-size: cover;
  padding: 16px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.About__card {
  position: relative;
  left: 0;
  top: 0;
  height: 100%;
  max-width: 500px;
  min-width: 350px;
  padding: 24px;
  line-height: 1.5rem;
  text-indent: 2.5em each-line;
  text-align: center;
  letter-spacing: 0.2rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  overflow: auto;
  -ms-flex-pack: center;
      justify-content: center;
  color: var(--sea);
  background: rgba(100%, 100%, 100%, 0.796);
  -ms-flex-direction: column;
      flex-direction: column;
}

div.About__card > p > a {
  color: inherit;
}

@media (max-height: 650px) {
  .About__card {
    -ms-flex-pack: start;
        justify-content: flex-start;
  }
}


